<template>
    <el-dialog
        title=""
        :visible="showDialog"
        size="tiny"
    >
        <span>{{text}}</span>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="closeDialog"> </el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    props: {
        text: '',
        showDialog: false
    },
    methods: {
        closeDialog() {
            // 12mounted,,3$emit
           this.$emit('closePop');
        }
    }
}
</script>





